import React, { FC } from "react"
import { StandardLayout } from "../components/layout"
import { Container } from "reactstrap"
import { PageHeader } from "../components/layout/page-header"
import { graphql, PageProps } from "gatsby"
import { FileSystemNode } from "gatsby-source-filesystem"
import { ChildImageSharp, SiteMetadata, ChildMarkdownRemark } from "src/util"
import styles from "./about.module.scss"
import SEO from "src/components/seo"

export const pageQuery = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        joinFormUrl
      }
    }
    about: file(relativePath: { eq: "text/about.md" }) {
      childMarkdownRemark {
        html
      }
    }
  }
`

type Data = {
  front: FileSystemNode & ChildImageSharp
  site: {
    siteMetadata: SiteMetadata
  }
  about: FileSystemNode & ChildMarkdownRemark
}

const Page: FC<PageProps<Data>> = ({ data }) => {
  return (
    <StandardLayout>
      <SEO title="About" />
      <Container className="small-container" tag="main">
        <PageHeader pageTitle="About Us" />
        <article
          className={styles.aboutSection}
          dangerouslySetInnerHTML={{
            __html: data.about.childMarkdownRemark.html,
          }}
        />
      </Container>
    </StandardLayout>
  )
}

export default Page
